.navbar {
  background-color: none;
  margin-top: 40px;
  height: 60px;
  display: flex;
  justify-content: start;
  align-items: center;
}
.menuBars {
  margin-left: 0rem;
  font-size: 2rem;
  background: #0a5517;
  border-radius: 4px;
  color: white;
  display: flex;
  align-items: center;
  width: 130px;
}
.navMenu {
  background-color: #0a5517;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 268px;
  margin-left: 8px;
  left: -100%;
  transition: 850ms;
}
.navMenu.active {
  left: 0;
  transition: 350ms;
}
.navText {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0 8px 16px;
  height: 60px;
}
.navText a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}
.navText a:hover {
  background-color: #89da1f;
}

.navMenuItems {
  width: 100%;
}
.navbarToggle {
  background-color: white;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}
span {
  margin-left: 16px;
}
