.chatbot {
  position: fixed;
  bottom: 0;
  right: 15px;
  border: 2px solid black;
  z-index: 9;
  width: 304px;
  opacity: 1;
  transform-origin: bottom;
  transition: chatbot 0.26s ease;
  background-color: white;
}

@keyframes chatbot {
  to {
    opacity: 1;
    transform: initial;
  }
}

.logoifmg {
  width: "21";
  margin-left: 6px;
}

.headerRow {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: 32px;
  background-color: #19882c;
  padding: 10px;
  border-bottom: 2px solid black;
  cursor: pointer;
}

.chatbotTitle {
  font-size: 20px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: 800;
  color: white;
  margin: 2px 90px 0px 0px;
}

.header {
  background: #19882c;
  height: 198px;
  width: 100%;
  box-shadow: 0 36px 0 0 #0a5517;
  display: flex;
  align-items: center;
}

.logo {
  margin-left: 88px;
}

.menuLateral {
  background: #c4c4c4;
  margin-left: 88px;
  margin-right: 60px;
  margin-top: 68px;
  min-width: 174px;
  height: 752px;
}

.main {
  display: flex;
}

.cabecalho {
  margin-top: 68px;
  background: #c4c4c4;
  width: 1036px;
  height: 207px;
}

.conteudo {
  margin-top: 20px;
  background: #c4c4c4;
  width: 100%;
  height: 428px;
}

.messageBot {
  padding: 3%;
  width: 265px;
  height: 60px;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 6px;

  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 15px;
}

.messageClient {
  margin-top: 20px;
  padding: 3%;
  width: 265px;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
  border-radius: 6px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 15px;
  margin-left: 9px;
}

.gridBot {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: 38px;
  padding: 8px;
}

.footerSend {
  margin-top: 257px;
  background-color: #19882c;
  height: 42px;
  width: 100%;
  display: flex;
}

.send {
  margin: 10px 0 10px 10px;
  width: 220px;
}

.btnSend {
  height: 25px;
  width: 25px;
  background-color: red;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  padding: 2px;
  margin: 6px 0 0 10px;
}

.spanLink {
  color: #ff0202;
  cursor: pointer;
  font-size: 11px;
}

.spaceDiv {
  height: 5px;
}
